@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&family=Noto+Serif+JP:wght@400;500;700&display=swap");
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* ==============================================
ここから
=============================================== */
@media screen and (min-width: 768px) {
  .media {
    background: linear-gradient(to left, #333 0, #333 70%, transparent 70%, transparent 100%);
  }
}

@media screen and (min-width: 768px) {
  .media__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2.5rem;
    margin-right: auto;
    margin-left: auto;
  }
}

.media__img {
  width: min(100%, 60rem);
  aspect-ratio: 375/261;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .media__img {
    width: min(100%, 55.7rem);
    aspect-ratio: 557/389;
    margin: 0;
  }
}

.media__img img {
  height: 100%;
  object-fit: cover;
}

.media__content {
  padding: 2.4rem 2rem;
  background-color: #222;
}
@media screen and (min-width: 768px) {
  .media__content {
    width: min(100%, 51rem);
    padding: 11.5rem 0;
    background-color: rgba(51, 51, 51, 0);
    margin-left: 3rem;
  }
}

.media__main-title {
  font-size: 2rem;
  text-align: center;
  line-height: 1.45;
}
@media screen and (min-width: 768px) {
  .media__main-title {
    font-size: 2.5rem;
    line-height: 1;
    text-align: left;
  }
}

.media__text {
  line-height: 1.4375;
  padding-top: 2rem;
}
@media screen and (min-width: 768px) {
  .media__text {
    line-height: 2.1875;
    padding-top: 4rem;
  }
}

/* ----------------------
逆バージョン
------------------------- */
@media screen and (min-width: 768px) {
  .media--reverse {
    background: linear-gradient(to right, #333 0, #333 70%, transparent 70%, transparent 100%);
  }
}

@media screen and (min-width: 768px) {
  .media__wrapper--reverse {
    flex-direction: row-reverse;
  }
}

@media screen and (min-width: 768px) {
  .media__img--reverse {
    margin-left: 3rem;
  }
}