@use "global" as *;

/* ==============================================
ここから
=============================================== */
.media {
  @include mq() {
    background: linear-gradient(to left, #333 0, #333 70%, transparent 70%, transparent 100%);
  }
}

.media__wrapper {
  @include mq() {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2.5rem;
    margin-right: auto;
    margin-left: auto;
  }
}

.media__img {
  width: min(100%, 60rem);//中途半端な大きさのタブレット向けに指定すると良さそう
  aspect-ratio: 375/261;
  margin-right: auto;
  margin-left: auto;
  
  @include mq() {
    width: min(100%, 55.7rem);
    aspect-ratio: 557/389;
    margin: 0;
  }
}

.media__img img {
  height: 100%;
  object-fit: cover;
}

.media__content {
  padding: 2.4rem 2rem;
  background-color: #222;

  @include mq() {
    width: min(100%, 51rem);
    padding: 11.5rem 0;
    background-color: rgba(51, 51, 51, 0);
    margin-left: 3rem;//くっつかないように
  }
}

.media__main-title {
  font-size: 2rem;
  text-align: center;
  line-height: calc(29 / 20);

  @include mq() {
    font-size: 2.5rem;
    line-height: 1;
    text-align: left;
  }
}

.media__text {
  line-height: calc(23 / 16);
  padding-top: 2rem;

  @include mq() {
    line-height: calc(35 / 16);
    padding-top: 4rem;
  }
}
/* ----------------------
逆バージョン
------------------------- */
.media--reverse {
  @include mq() {
    background: linear-gradient(to right, #333 0, #333 70%, transparent 70%, transparent 100%);
  }
}

.media__wrapper--reverse {
  @include mq() {
    flex-direction: row-reverse;//左右反転
  }
}

.media__img--reverse {
  
  @include mq() {
    margin-left: 3rem;//くっつかないように
  }
}